  .topnav a {
    float: left;
    display: block;
    color: #999595;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    font-weight: 500;
    font-size: 21px;
    line-height: 120%;
    letter-spacing: -0.03em; }
    .topnav a:hover {
      color: #4c4949; }
  .topnav a.active {
    border-bottom: 2px solid #181717;
    color: #181717; }
  .topnav .icon {
    display: none; }

@media screen and (max-width: 600px) {
  .topnav a:not(:first-child) {
    display: none; }
  .topnav a.icon {
    float: right;
    display: block; }
  .topnav.responsive {
    position: relative; }
    .topnav.responsive a.icon {
      position: absolute;
      right: 0;
      top: 0; }
    .topnav.responsive a {
      float: none;
      display: block;
      text-align: left; } }

